import * as React from 'react'
import {BrowserRouter as Router, Route, Switch, Redirect } from 'react-router-dom'
import App from '../containers/App'

const RouterMap = () => (
  <Router>
    <Switch>
      <Route path="/sider">
        <App />
      </Route>
      <Redirect from="" to="/sider/register" />
    </Switch>
  </Router>
)

export default RouterMap



